.header {
  @media (min-width: $desktop-width) {
    min-height: 696px;

    background-image: linear-gradient(0deg, $basic-green-overlay, $basic-green-overlay), url("../img/index-background-desktop.webp");
    background-size: 50%;
    background-position: top right;
    background-repeat: no-repeat;

    @media (min-resolution: 2dppx) {
      background-image: linear-gradient(0deg, $basic-green-overlay, $basic-green-overlay), url("../img/index-background-desktop@2x.webp");
    }
  }
}

.header__wrapper {
  position: relative;

  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    min-height: 100px;
  }

  @media (min-width: $desktop-width) {
    min-height: 165px;
  }
}

.header__logo {
  display: flex;
  align-items: center;
  min-height: 66px;

  &[href]:hover,
  &[href]:focus {
    opacity: 0.8;
  }

  &[href]:active {
    opacity: 0.6;
  }
}

.header__logo-icon {
  margin-right: 55px;

  @media (min-width: $tablet-width) {
    margin-right: 0;
    width: 174px;
    height: auto;
  }

  @media (min-width: $desktop-width) {
    margin-right: 0;
    width: 202px;
    height: auto;
  }
}

.header__logo-name {
  @media (min-width: $tablet-width) {
    display: none;
  }
}
