.form__wrapper {
  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 569px 569px;
  }
}

.form__fieldset {
  margin: 0;
  padding: 0;

  border: none;

  &--cat {
    padding-bottom: 46px;
  }

  &--goal {
    padding-bottom: 46px;
  }

  &--contacts {
    padding-bottom: 24px;
    grid-column: 1 / -1;
  }

  &--comment {
    padding-bottom: 23px;
    grid-column: 1 / -1;
  }

  &--extra {
    padding-bottom: 34px;
    grid-column: 1 / -1;
  }

  //TODO переделать отступы
  //TODO не модификатор а элемент
}

.form__fieldset-title {
  margin-bottom: 36px;

  @include font-oswald;
  font-size: 24px;
  line-height: 26px;
  color: $black;
}

.form__item {
  margin: 0;
}

.form__label,
.form__input::placeholder {
  @include font-oswald;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}

.form__label--dark {
  color: $title-dark;
}

.form__input::placeholder {
  color: $text-dark;
}

.form__submit {
  margin-bottom: 21px;
}

.form__required-text {
  margin: 0;

  line-height: 16px;
  color: $black;
  text-align: center;
}
